  font-size: 0.75rem !important; }

.text-s {
  font-size: 0.875rem !important; }

.text-m {
  font-size: 1rem !important; }

.text-l {
  font-size: 1.5rem !important; }

.text-primary {
  color: #2A8356 !important; }

.text-primary-light {
  color: #CDDDCD !important; }

.text-primary-lighter {
  color: #EDF4EE !important; }

.text-secondary {
  color: #121212 !important; }

.text-secondary-light {
  color: #0000009A !important; }

.text-info {
  color: #707070 !important; }

.text-info-light {
  color: #00000062 !important; }

.text-light {
  color: #FFFFFF !important; }

.text-warning {
  color: #E3006E !important; }

.font-primary {
  font-family: "Lato", sans-serif !important; }

.font-secondary {
  font-family: "Roboto Mono", monospace; }

.font-regular {
  font-weight: 400; }

.font-bold {
  font-weight: 700; }

.lh-s {
  line-height: 1.2; }

.lh-m {
  line-height: 1.5; }

.text-x-center {
  text-align: center; }

.text-x-right {
  text-align: right; }

.text-x-left {
  text-align: left; }

.h2 {
  font-size: 1.5rem;
  line-height: 1.2;
  font-weight: 400; }
  .h2-b {
    font-size: 1.5rem;
    line-height: 1.2;
    font-weight: 700; }

.h3 {
  font-size: 1rem;
  line-height: 1.2;
  font-weight: 400; }
  .h3-b {
    font-size: 1rem;
    line-height: 1.2;
    font-weight: 700; }
  .h3-m {
    font-size: 1rem;
    line-height: 1.5; }

.h4 {
  font-size: 0.875rem;
  line-height: 1.2;
  font-weight: 400; }
  .h4-m {
    font-size: 0.875rem;
    line-height: 1.5;
    font-weight: 400; }

.h5 {
  font-size: 0.75rem;
  line-height: 1.2;
  font-weight: 400; }

.subTitle {
  font-size: 1.25rem;
  line-height: 1.2;
  font-weight: 700; }

* {
  box-sizing: border-box;
  list-style: none; }

body {
  font-family: "Lato", sans-serif;
  color: #121212; }

a {
  color: #121212;
  text-decoration: none;
  display: block; }
  a:hover {
    text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

button:focus {
  outline: 0; }

a.text-secondary:hover,
a.text-secondary:focus {
  color: #121212 !important; }

.material-icons:focus {
  outline: 0; }

.cell-primary-lighter {
  background-color: #EDF4EE;
  border: 1px solid #CDDDCD; }

.cell-light {
  background-color: #FFFFFF;
  border: 1px solid #CDDDCD; }

.cell-lighter {
  background-color: #FFFFFF;
  color: #2A8356;
  border: 1px solid #2A8356; }

.main {
  width: 80%; }

.container {
  width: 918px;
  margin: 0 auto;
  padding: 0; }

.tool {
  width: 88px;
  margin-right: -8px; }

.point {
  font-family: "Roboto Mono", monospace;
  color: #2A8356;
  background-color: #EDF4EE; }

.main .icon {
  cursor: pointer;
  border-bottom: 1px solid transparent;
  transition: 0.5s; }
  .main .icon:hover {
    border-bottom: 1px solid #2A8356; }

.material-icons {
  vertical-align: middle;
  transition: .5s; }

.icon-modal {
  padding: 0;
  border: 0;
  background-color: transparent; }

.btn {
  padding: 8px 32px;
  font-size: 16px;
  line-height: 19px;
  border-radius: 3px; }

.btn-primary {
  background-color: #2A8356;
  color: #FFFFFF;
  border: 1px solid #2A8356;
  transition: .5s; }
  .btn-primary:hover {
    background-color: #EDF4EE;
    color: #2A8356; }

.btn-light {
  background-color: #FFFFFF;
  color: #2A8356;
  border: 1px solid #2A8356;
  transition: .5s; }
  .btn-light:hover {
    background-color: #EDF4EE;
    color: #2A8356; }

.btn-light {
  background-color: #FFFFFF;
  color: #2A8356;
  border: 1px solid #EDF4EE;
  transition: .5s; }
  .btn-light:hover {
    background-color: #2A8356;
    color: #FFFFFF; }

.btn-warning {
  background-color: #FFFFFF;
  color: #E3006E;
  border: 1px solid #E3006E;
  transition: .5s; }
  .btn-warning:hover {
    background-color: #E3006E;
    color: #FFFFFF; }

.btn-disable {
  background-color: #FFFFFF;
  color: #EDF4EE;
  border: 1px solid #EDF4EE; }

.input-light {
  background-color: #FFFFFF;
  padding: 16px;
  font-family: "Lato", sans-serif;
  color: #121212;
  border: 1px solid #121212;
  border-radius: 2px; }

.input-lighter {
  background-color: #EDF4EE;
  padding: 16px;
  font-family: "Lato", sans-serif;
  color: #121212;
  border: 0;
  border-radius: 2px; }

.edit-input {
  width: 432px; }

.nav {
  min-width: 20%;
  min-height: 100vh;
  position: fixed;
  background-color: #EDF4EE; }

.logo {
  width: 151px;
  height: 40px;
  background-image: url("../images/logo-ANSWER.svg");
  overflow: hidden;
  white-space: nowrap;
  text-indent: 101%; }

.menu {
  color: #121212; }

.menu:hover .menuItem {
  background-color: #CDDDCD; }

.menu:active .menuItem {
  color: #2A8356;
  border-left: 4px solid #2A8356; }

.menuItem {
  border-left: 4px solid transparent; }

.currentUser {
  position: relative;
  bottom: 0; }

.userPhoto {
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.welcome {
  line-height: 100vh; }

.wrap {
  width: 100%; }

.sidebar {
  width: 320px; }

.articleItem {
  cursor: pointer;
  transition: .5s; }
  .articleItem:hover, .articleItem:focus {
    background-color: #EDF4EE;
    color: #121212; }

.authorNameLight {
  color: #0000009A; }

.article {
  width: 584px;
  position: relative; }

.articleCard {
  border-top: 0;
  border-bottom: 0; }

.authorPhoto {
  width: 40px;
  height: 40px;
  border-radius: 50%; }

.articleContent {
  border-bottom: 1px solid #CDDDCD; }

.attachFile {
  cursor: pointer; }

.commentCard .articleContent {
  border-bottom: 0; }

.commentCard {
  border-top: 0; }

.replyArea {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0; }

.fontSelect {
  background-color: #EDF4EE;
  border: 0; }

.replytyping {
  position: relative; }

.replyContent {
  width: 100%;
  height: 292px;
  border: 1px solid #CDDDCD;
  resize: none; }

.replyAreaBtn {
  position: absolute;
  right: 24px;
  bottom: 16px; }

.adminList {
  width: 100%; }

.adminListItem {
  border-bottom: 1px solid #CDDDCD; }

.adminListTitle {
  border-bottom: 1px solid #121212; }

.adminFooter .icon {
  border-bottom: 0; }

.adminFooter .icon:hover {
  border-bottom: 0; }

.pIcon-before {
  border-radius: 2px 0 0 2px; }

.pIcon-next {
  border-radius: 0 2px 2px 0; }

.pagination {
  color: #2A8356;
  padding: 8px 12px;
  transition: .5s; }

.pIcon-a {
  line-height: 1.2;
  padding-left: 4px;
  padding-right: 4px; }

.pagination:hover,
.pagination:focus {
  background-color: #2A8356;
  color: #FFFFFF; }

.expand[aria-expanded="true"] .material-icons {
  transform: rotate(180deg); }

.viewPhoto {
  width: 141px;
  height: 141px; }

.editBlock {
  border-bottom: 1px solid #CDDDCD; }

.modal-footer {
  border: 0; }
